<template>
    <div class="box">
        <div class="estimate">预估收入<span>{{ total | bigNumber }}<i>元</i></span>
            <div class="month">
                <div @click="showSelect=true">{{ getMonth(selectMonth) }}</div>
                <van-actionsheet
                    v-model="showSelect"
                    :actions="monthStr"
                    @select="onSelect"
                />
            </div>
        </div>
        <div class="list">
            <table>
                <thead><tr><th>日期</th><th>邀请人数</th><th>预估拉新</th><th>预估分红</th></tr></thead>
                <tbody><tr
                    v-for="(item, i) in list"
                    :key="i">
                    <td>{{ item.date }}</td>
                    <td>{{ item.invitedCount }}人</td>
                    <td>{{ item.revenueOfInvited | bigNumber }}元</td>
                    <td>{{ item.revenueOfBonus | bigNumber }}元</td>
                </tr></tbody>
            </table>
            <p
                class="empty"
                v-show="!list.length">月赚千元，从邀请第一个好友开始</p>
        </div>
    </div>
</template>

<script>
import { getDetail } from '@/api/partner';
import { format } from '@/utils/time';

const initMonth = +format(Date.now(), 'YYYYMM');

export default {
    data() {
        return {
            list: [],
            selectMonth: initMonth,
            monthStr: [],
            showSelect: false,
        };
    },
    computed: {
        total() {
            return this.list.reduce((sum, item) =>
                sum + item.revenueOfInvited + item.revenueOfBonus, 0);
        },
    },
    watch: {
        selectMonth(v) {
            this.setMonthInfo(v);
        },
    },
    filters: {
        bigNumber(v) {
            if (v > 10000) {
                return parseInt(v, 10);
            }
            return parseInt(v * 100, 10) / 100;
        },
    },
    created() {
        this.setMonthInfo(this.selectMonth);
        this.updateMonths();
    },
    methods: {
        setMonthInfo(month) {
            getDetail({
                month,
            }).then((ret) => {
                this.list = ret;
            }).catch(() => {
                this.$error();
            });
        },
        getMonth(v) {
            return v === initMonth ? '本月' : `${parseInt(v / 100, 10)}年${v % 100}月`;
        },
        updateMonths() {
            let month = 10;
            let year = 201900;
            const arr = [];
            let ret = 201910;
            while (ret < this.selectMonth) {
                arr.unshift({ name: this.getMonth(ret) });
                if (month === 12) {
                    month = 1;
                    year += 100;
                } else {
                    month += 1;
                }
                ret = year + month;
            }
            this.monthStr = [{ name: '本月' }, ...arr];
        },
        onSelect(v) {
            this.$o2o({
                e_c: '月份筛选',
                e_a: '点击',
                e_n: '地推合伙人平台收益页',
            });
            if (v.name === '本月') {
                this.selectMonth = +format(Date.now(), 'YYYYMM');
            } else {
                const ret = v.name.match(/\d+/g);
                this.selectMonth = (ret[0] * 100) + +ret[1];
            }
            this.showSelect = false;
        },
    },
};
</script>

