<template>
    <div class="income">
        <div class="top">
            <h4>收益明细</h4>
            <p class="title">平台会审核邀请来的新用户的质量，记得提醒用户完成相应任务并持续使用想看，才能获得更高额度奖励。</p>
        </div>
        <div class="content">
            <div class="total">
                <div class="total__top">
                    <div class="total__number">￥{{ info.bonusCanWithdraw | bigNumber }}</div>
                    <div class="total__info">
                        <p class="total__title">待提现分红（元）</p>
                        <p class="total__subtitle">还未提现的分红每月累加</p>
                    </div>
                </div>
                <button
                    :class="[info.inBlacklist ? 'total_btn-disabled':'','total__btn']"
                    @click="goWithdraw">立即提现</button>
            </div>
            <div class="apportion">
                <div class="apportion__number">￥{{ info.bonusInAudit | bigNumber }}</div>
                <div class="apportion__text">
                    <p class="apportion__title">待审核分红（元）</p>
                    <p class="apportion__subtitle">{{ info.bonusDay }}号结算统一审核当月分红</p>
                </div>
            </div>
            <div class="apportion">
                <div class="apportion__number">￥{{ info.bonusAuditFail | bigNumber }}</div>
                <div class="apportion__text">
                    <p class="apportion__title">审核失败分红（元）</p>
                    <p class="apportion__subtitle">邀请无效用户将不发放分红</p>
                </div>
            </div>
            <p class="tip">特别提醒：每月{{ info.bonusDay }}号结算当月分红和收益，
            当月最后一天发放分红，{{ info.bonusDay }}号-月末分红将算入下个月的收入中
            </p>
            <income-list />
            <div
                class="footer"
                @click="back">
                <button>返回继续拉人</button>
            </div>
        </div>
    </div>
</template>

<script>
import { getIncome } from '@/api/partner';
import { openCommonWebViewByPath, quitCurrentWebview, toast } from '@/utils/jsApiV2/client';
import IncomeList from './IncomeList';

export default {
    components: {
        IncomeList,
    },
    data() {
        return {
            info: {
                bonusCanWithdraw: 0,
                bonusInAudit: 0,
                bonusAuditFail: 0,
                bonusDay: 0,
                inBlacklist: false,
            },
        };
    },
    created() {
        getIncome().then((ret) => {
            this.info = ret;
        }).catch(() => {
            this.$error();
        });
        this.$o2o({
            e_c: '收益页曝光',
            e_a: '曝光',
            e_n: '地推合伙人平台收益页',
        });
        this.$root.hidePlaceholder();
    },
    filters: {
        bigNumber(v) {
            if (v > 10000) {
                return parseInt(v, 10);
            }
            return parseInt(v * 100, 10) / 100;
        },
    },
    methods: {
        goWithdraw() {
            if (this.info.inBlacklist) {
                toast('账号存在异常，无法进行提现！');
                return;
            }
            this.$o2o({
                e_c: '立即提现',
                e_a: '点击',
                e_n: '地推合伙人平台收益页',
            });
            openCommonWebViewByPath('/mobile-v2/act/partner/withdraw');
        },
        back() {
            this.$o2o({
                e_c: '返回上一级',
                e_a: '点击',
                e_n: '地推合伙人平台收益页',
            });
            quitCurrentWebview();
        },
    },
};
</script>

